import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

import {
  Container,
  Heading,
  InputContainer,
  SelectContainer,
  Label,
  Select,
  SearchButton,
  ButtonText,
} from "../../styles/SearchStyle";

// data
import searchData from "../../data/search.json";

// icon
import SearchIcon from "@mui/icons-material/Search";

const ReturnReportSearch = () => {
  const navigate = useNavigate();

  const { periods, programs, countries, universities, informations } =
    searchData;

  const [selectedPeriod, setSelectedPeriod] = useState("");
  const [selectedProgram, setSelectedProgram] = useState("");
  const [selectedCountry, setSelectedCountry] = useState("");
  const [selectedUniversity, setSelectedUniversity] = useState("");
  const [selectedInfo, setSelectedInfo] = useState("");

  const handlePeriodChange = (e) => {
    const value = e.target.value;
    setSelectedPeriod(value);
  };

  const handleProgramChange = (e) => {
    const value = e.target.value;
    setSelectedProgram(value);
  };

  const handleCountryChange = (e) => {
    const value = e.target.value;
    setSelectedCountry(value);
    setSelectedUniversity("");
  };

  const handleUniversityChange = (e) => {
    const value = e.target.value;
    setSelectedUniversity(value);
  };

  const handleInfoChange = (e) => {
    const value = e.target.value;
    setSelectedInfo(value);
  };

  const handleSearchClick = () => {
    if (checkRequiredValues()) {
      replaceUnselectedWithAll();

      const selected_data = {
        period: `${selectedPeriod}`,
        program: `${selectedProgram}`,
        country: `${selectedCountry}`,
        university: `${selectedUniversity}`,
        selected_information: `${informations[selectedInfo]}`,
      };

      navigate("/report-result", {
        state: { selected_data },
      });
    }
  };

  const checkRequiredValues = () => {
    if (selectedCountry === "") alert(" .");
    else if (selectedUniversity === "") alert(" .");
    else if (selectedInfo === "") alert("  .");
    else return true;
  };

  const replaceUnselectedWithAll = () => {
    if (selectedPeriod === "") setSelectedPeriod("");
    if (selectedProgram === "") setSelectedProgram("");
  };

  return (
    <Container>
      <Heading> </Heading>
      <InputContainer>
        <SelectContainer>
          <Label htmlFor="period"></Label>
          <Select
            id="period"
            value={selectedPeriod}
            onChange={handlePeriodChange}
          >
            {periods.map((period, index) => (
              <option key={period} value={period}>
                {period}
              </option>
            ))}
          </Select>
        </SelectContainer>
        <SelectContainer>
          <Label htmlFor="program"> </Label>
          <Select
            id="program"
            value={selectedProgram}
            onChange={handleProgramChange}
          >
            {programs.map((program, index) => (
              <option key={program} value={program}>
                {program}
              </option>
            ))}
          </Select>
        </SelectContainer>
        <SelectContainer>
          <Label htmlFor="country"> *</Label>
          <Select
            id="country"
            required
            value={selectedCountry}
            onChange={handleCountryChange}
          >
            <option disabled value="">
               
            </option>
            {countries.map((country, index) => (
              <option key={country} value={country}>
                {country}
              </option>
            ))}
          </Select>
        </SelectContainer>
        <SelectContainer>
          <Label htmlFor="university"> *</Label>
          {selectedCountry === "" ? (
            <Select>
              <option value=""> </option>)
            </Select>
          ) : (
            <Select
              id="university"
              value={selectedUniversity}
              onChange={handleUniversityChange}
            >
              <option value=""> </option>
              {universities[selectedCountry].map((university, index) => (
                <option key={university} value={university}>
                  {university}
                </option>
              ))}
            </Select>
          )}
        </SelectContainer>
        <SelectContainer>
          <Label htmlFor="selectedInfo">   *</Label>
          <Select
            id="selectedInfo"
            required
            value={selectedInfo}
            onChange={handleInfoChange}
          >
            <option disabled value="">
                
            </option>
            {Object.keys(informations).map((info, index) => (
              <option key={info} value={info}>
                {info}
              </option>
            ))}
          </Select>
        </SelectContainer>
      </InputContainer>
      <SearchButton onClick={handleSearchClick}>
        <ButtonText></ButtonText>
        <SearchIcon />
      </SearchButton>
    </Container>
  );
};

export default ReturnReportSearch;
